.calendar {
  border: 1px solid red;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  height: auto;
  margin: 15px 0;
}

.stats {
  border: 1px solid red;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  height: auto;
  margin: 15px 0;
}
.stats > div {
  border: 1px solid red;
  width: 45%;
}

.stats > div > a > img {
  width: 100%;
}
