.container {
  width: 100%;
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
}

.container > div {
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: column;
  position: relative;
}

.line {
  width: 1px;
  height: 30vh;
  background: var(--light-slate);
  margin-top: 20px;
}

.icon_Link {
  margin-top: 15px;
  cursor: pointer;
  transition: var(--transition);
}

.icon_Link:hover {
  color: var(--green);
  transform: translateY(-3px);
}
