.container {
  top: 10px;
  left: 0;
  position: fixed;
  width: 100%;
  background-color: var(--transparent-navy);
  box-shadow: var(--navy-shadow);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 110;
}

.left {
  padding-left: 50px;
}
.left > a {
  color: var(--green);
  font-size: var(--fz-xxl);
  font-weight: bold;
  font-family: var(--font-sans);
}

.right {
  padding-left: 50px;
}
.link {
  font-size: var(--fz-sm);
  color: ar(--light-slate);
  cursor: pointer;
  margin-left: 25px;
}

.link:hover {
  color: var(--green);
}
.resumeBtn {
  outline: none;
  font-size: var(--fz-xs);
  background: transparent;
  border: 1px solid var(--green);
  color: var(--green);
  border-radius: 5px;
  padding: 10px 12px;
  cursor: pointer;
  margin: 0 25px;
}
.resumeBtn:hover {
  background: var(--green-tint);
}
