.container1 {
  border: 1px solid red;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin: 5rem 0;
}
.main {
  border: 1px solid hotpink;
  width: 90%;
  padding: 15px;
  margin: 10px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  text-align: center;
  column-gap: 1rem;
  row-gap: 1rem;
}
.main > div {
  border: 2px solid green;
}

.main > div > img {
  transition: transform 0.5s ease;
  width: 100%;
  max-width: 200px;
}
.main > div > img:hover {
  padding: 0;
  margin: 0;
  transform: scale(1.1);
}
.main > div > p {
  font-size: var(--fz-xxl);
  transition: transform 1s ease;
}
.main > div > p:hover {
  color: var(--green);
  transform: scale(1.3);
}
.image:hover ~ .title {
  color: var(--green);
  transform: scale(1.3);
}

/* githubStats */
.container2 {
  border: 1px solid red;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin: 5rem 0;
}
